import React from 'react'

import "./nextLevelPopup.scss"
import Button from './../../Components/Button'
import Image from './../../Components/Image'


import iconCoin from './../../Components/images/coin.svg'
import congratulationsText from './../../Components/images/сongratulations.svg'


function NextLevelPopup(props) {

    const {ranks, users, userId} = props;
    const currentUserRank = users[userId].rank;
    const rankName = ranks[currentUserRank].rank_name;
    const rankBonus = ranks[currentUserRank].prize_bonus;

    function getBonusCoins() {


    }

    return (
        <div className="popup_nextLevel">
            <img className="popup_nextLevel__rankImg" src={ranks[currentUserRank].urlImg} alt={rankName}/>
            <img className="popup_nextLevel__сongratulations" src={congratulationsText} alt="Поздравляем"/>
            <p className="popup_nextLevel__text">Вы достигли нового уровня - {rankName} <br/> Ваш приз {rankBonus} фишек</p>
            <Button className="popup_nextLevel__button" onClick={(e) => getBonusCoins()}>
                <Image className="popup_nextLevel__coins" src={iconCoin} width={25} height={25} alt="фишки"/>
                <span>{rankBonus}</span>
            </Button>
        </div>
    );
}

export default NextLevelPopup;