import React from 'react';
import { NavLink} from 'react-router-dom'

import "./menu.scss"

function Menu(props) {

    const {closeHeaderText} = props;


    return (
        <nav className="menu">
            <ul className="menu_list">
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__work" to="/work/" onClick={(e) => closeHeaderText()}>
                        <div className="menu_img"/>
                        <p className="menu_name">Работа</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__bonuses" to="/bonuses/" onClick={(e) => closeHeaderText()}>
                        <div className="menu_img"/>
                        <p className="menu_name">Бонусы</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__main" exact to="/" onClick={(e) => closeHeaderText()}>
                        <div className="menu_img"/>
                        <p className="menu_name">Лобби</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__tournament" to="/tournaments/" onClick={(e) => closeHeaderText()}>
                        <div className="menu_img"/>
                        <p className="menu_name">Турниры</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__rating" to="/rating/" onClick={(e) => closeHeaderText()}>
                        <div className="menu_img"/>
                        <p className="menu_name">Рейтинг</p>
                    </NavLink>
                </li>
            </ul>
        </nav>
    )
}


export default Menu