import React from "react"
import {NavLink} from "react-router-dom";
import Button from "./../../Components/Button"



import "./changePass.scss"



class ChangePass extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            password :'',
            password_re:'',
            error_text: '',
            password_has_error:false
        };

    }



    checkPassword() {
        if(!this.state.password || this.state.password !== this.state.password_re) {
            this.setState({password_has_error:true});
            console.log("Пароли не совпадают");

        }
        else {
            this.setState({password_has_error:false});
            this.setState({error_text:""});
            console.log("Пароли Совпадают");
        }
    }

    handleChange(event) {
        const { name, value } = event.target;

        this.setState({
            [name] : value
        }, () => {
            if (name === 'password' || name === 'password_re') this.checkPassword();
        });
    }


    handleSubmit(event) {
        event.preventDefault();

        if( (this.state.password === "") || (this.state.password === undefined) ){
            this.setState({error_text:"Введите новый пароль, минимум 8 символов"});
            return;
        }

        if( this.state.password.length < 8 ){
            this.setState({error_text:"Пароль должен быть больше 8 знаков"});
            return;
        }
        if( this.state.password.length > 30 ){
            this.setState({error_text:"Пароль должен быть меньше 30 знаков"});
            return;
        }

        if( this.state.password !== this.state.password_re){
            this.setState({error_text:"Пароли не совпадают, перепроверте значения"});
            return;
        }


        if(this.state.password_has_error){
            console.log("We have problem");
            return
        }

        alert("Change Pass");
        // TODO: will submit the form here
    }


    componentDidMount() {
        this.props.showHeaderText("Сменить пароль");
        this.props.functionCorrectingPersonalInformation();
    }



    render(props) {





        return (

            <div className="changePass">
                <form className="changePass_form" onSubmit={(event) => this.handleSubmit(event)}>

                    <label className="label_element" htmlFor="pass">
                        <input id="pass" className="input_text input_pass" type="password" name="password" value={this.state.password}
                               onChange={(event)=>this.handleChange(event)}
                               placeholder="Новый пароль"/>
                    </label>
                    <label className="label_element" htmlFor="pass-confirm">
                    <input id="pass-confirm" className="input_text input_repeatPass" type="password" name="password_re"
                           value={this.state.password_re}
                           onChange={(event)=>this.handleChange(event)}
                           placeholder="Повторите новый пароль"/>
                           <p className="text_error">{this.state.error_text}</p>
                    </label>

                    <Button className="btn btn_changePass">Сменить пароль</Button>

                </form>

                <NavLink className="link_login" to={'/login/'}>Войти в аккаунт</NavLink>

            </div>
        )
    }

}


export default ChangePass;