import React from "react"

class ModuleNewsletters extends React.Component {

    constructor(props) {

        super(props);
        this.state = {
            isChecked: props.isUserWantNews,
        };
    }



    toggleChange = () => {
        this.setState({
            isChecked: !this.state.isChecked,
        });
    };

    handleSubmit(event) {
        console.log("You change subscribe");
        event.preventDefault();
    }



    render() {
        return (
            <form className="module_checkbox" onSubmit={this.handleSubmit}>
                <input id="newsletters" className="module_checkbox__input"
                       type="checkbox"
                       checked={this.state.isChecked}
                       onChange={this.toggleChange}/>
                <label className="module_checkbox__text" htmlFor="newsletters">
                    <p>Я хочу получать бонусные и новостные рассылки от казино</p>
                </label>
            </form>
        )
    }
}




export default ModuleNewsletters